<template>
    <div class="multi-legend" id="gv-multi-legend-panel">
    <vue-draggable-resizable :w="width"  :resizable="false" >
        <div>
        <gv-title :title="title" :divId="'gv-multi-legend-panel'"></gv-title>
        <gv-iframe-panel class="gv-iframe-panel" visible="true" :src="src" :html="html" :height="height" :width="width"></gv-iframe-panel>
        </div>
    </vue-draggable-resizable> 
    </div>
</template>


<script>
    import * as config from '../config'
    import Vue from 'vue'

    Vue.component('gv-title', () => import('./Title.vue'))

    export default {
        name: 'gv-multi-legend-panel',
        props: ['title','html', 'src', 'visible', 'title', 'width', 'height']
    }

</script>

<style scoped>
    .multi-legend {
        position: absolute;
        margin-top: 100px;
        margin-left: 10px;
        background-color: #fff;
        z-index: 800;
    }
    
.gv-iframe-panel {
    background-color: #fff;
}

</style>


